import React from 'react'
import fotoNano from '../assets/img/contacto.jpeg'
const Contact = () => {
    return (
        <div className="relative bg-white">
        <div className="lg:absolute lg:inset-0">
          <div className="lg:absolute lg:inset-y-0 lg:right-0 lg:w-1/2 lg:mt-12">
            <img className="h-56 w-full object-cover lg:absolute lg:h-full" src={fotoNano} alt="" />
          </div>
        </div>
        <div className="relative pt-12 pb-16 px-4 sm:pt-12 sm:px-6 lg:px-8 lg:max-w-7xl lg:mx-auto lg:grid lg:grid-cols-2">
          <div className="lg:pr-8">
            <div className="max-w-md mx-auto sm:max-w-lg lg:mx-0">
              <h2 className="text-3xl leading-9 font-extrabold tracking-tight sm:text-3xl sm:leading-10">
              CONTACTAR CON NANO ALÚTIZ
              </h2>
              <p className="mt-4 text-lg leading-7 text-gray-500 sm:mt-3">
              Me gustaría ayudarte a mejorar por dentro para que se note por fuera. Si necesitas más información, ponte en contacto conmigo a través del siguiente formulario o llámame.              </p>
              <form action="#" method="POST" className="mt-9 grid grid-cols-1 row-gap-6 sm:grid-cols-2 sm:col-gap-8">
                <div>
                  <label for="first_name" className="block text-sm font-medium leading-5 text-gray-700">NOMBRE</label>
                  <div className="mt-1 relative rounded-md shadow-sm">
                    <input id="first_name" className="form-input block w-full transition ease-in-out duration-150 sm:text-sm sm:leading-5" />
                  </div>
                </div>
                <div>
                  <label for="last_name" className="block text-sm font-medium leading-5 text-gray-700">Apellidos</label>
                  <div className="mt-1 relative rounded-md shadow-sm">
                    <input id="last_name" className="form-input block w-full transition ease-in-out duration-150 sm:text-sm sm:leading-5" />
                  </div>
                </div>
                <div className="sm:col-span-2">
                  <label for="email" className="block text-sm font-medium leading-5 text-gray-700">CORREO ELECTRÓNICO</label>
                  <div className="mt-1 relative rounded-md shadow-sm">
                    <input id="email" type="email" className="form-input block w-full transition ease-in-out duration-150 sm:text-sm sm:leading-5" />
                  </div>
                </div>
                <div className="sm:col-span-2">
                  <div className="flex justify-between">
                    <label for="phone" className="block text-sm font-medium leading-5 text-gray-700">TELÉFONO</label>
                    <span className="text-sm leading-5 text-gray-500">Opcional</span>
                  </div>
                  <div className="mt-1 relative rounded-md shadow-sm">
                    <input id="phone" className="form-input block w-full transition ease-in-out duration-150 sm:text-sm sm:leading-5" />
                  </div>
                </div>
                <div className="sm:col-span-2">
                  <div className="flex justify-between">
                    <label for="how_can_we_help" className="block text-sm font-medium leading-5 text-gray-700">MENSAJE</label>
                    <span className="text-sm leading-5 text-gray-500">Max. 500 characters</span>
                  </div>
                  <div className="mt-1 relative rounded-md shadow-sm">
                    <textarea id="how_can_we_help" rows="4" className="form-textarea block w-full transition ease-in-out duration-150 sm:text-sm sm:leading-5"></textarea>
                  </div>
                </div>
               
                
                <div className="text-right sm:col-span-2">
                  <span className="inline-flex rounded-md shadow-sm">
                    <button type="submit" className="inline-flex justify-center py-2 px-4 border border-transparent text-sm leading-5 font-medium rounded-md text-white bg-blue-nano  hover:bg-blue-nanodark focus:outline-none focus:border-indigo-700 focus:shadow-outline-indigo active:bg-indigo-700 transition duration-150 ease-in-out">
                      Enviar
                    </button>
                  </span>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    )
}

export default Contact
